import React from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';


ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend
);

export const options = {
    responsive: true,
};

export function Charts(props) {
console.log(props);
const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];
const fake = [56, 78, 67, 58, 58, 47, 45];

const data = {
    labels,
    datasets: [
        {
            fill: true,
            label: props.label,
            data: fake,
            borderColor: 'rgba(124,22,78,0.95)',
            backgroundColor: 'rgba(201,127,156,0.5)',
        },
    ],
};


    return (
        <div className='for_chart'>
            <Line options={options} data={data} />
        </div>
    );
}

export default Charts;